@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.nav-btns {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-around;
}

.nav-btn {
  flex-grow: 1;
  min-height: 3em;
  background-color: rgb(184, 103, 103);
  font-size: larger;
  font-weight: lighter;
  color: rgb(61, 61, 61);
  letter-spacing: 1px;
  text-decoration: none;
  border: none;
}

.nav-btn:hover {
  border-bottom: 3px solid rgb(26, 114, 216);
  color: black;
}

.header-container {
  display: grid;
}

.link-header {
  align-self: left;
  justify-self: left;
}

.logo {
  height: 100px;
  width: 130px;
  padding: 10px 10px;
}

form {
  display: block;
  align-self: top;
  justify-self: left;
}

.header-input {
  width: 300px;
  transition: all 1s ease-in-out;
  margin-bottom: 10px;
  border-radius: 6px;
  border: none;
  outline: none;
  background: rgb(64, 109, 168);
  height: 35px;
  color: whitesmoke;
  text-align: center;
  font-size: 18px;
  letter-spacing: 1px;
}

.header-input:focus {
  width: 40%;
}

.header-log-search {
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: 70px;
}

.header-left {
  justify-self: left;
}

.header-button {
  justify-self: right;
  align-self: center;
}

.header-button1 {
  justify-self: right;
  align-self: center;
  border-radius: 8px;
  padding: 10px 30px;
  background-color: white;
  color: black;
  border: 2px solid red;
  font-size: larger;
}

.header-button1:hover {
  background-color: rgb(184, 103, 103);
  color: white;
  box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
    0 17px 50px 0 rgba(0, 0, 0, 0.19);
}
#blue {
  background-color: rgb(26, 114, 216);
  color: white;
}

.footer-button {
  width: 100%;
  height: 3rem;
  border-radius: 8px;
  background-color: rgb(184, 103, 103);
  color: black;
  border: 2px solid red;
  font-size: larger;
}

.footer-button:hover {
  background-color: red;
  color: white;
  box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
    0 17px 50px 0 rgba(0, 0, 0, 0.19);
}

select {
  margin: 10px;
  margin-top: 0px;
  height: 30px;
  width: 60px;
  border: none;
  background-color: rgb(184, 103, 103);
  color: whitesmoke;
  outline: none;
  border-radius: 6px;
  text-align: center;
}

.header-button2{
  margin-left: 6px;
  margin-right: 6px;
}
html{
  background-color: rgb(42, 41, 39);
}
body{
  background-color: white;
}
.general-select {
    width: 120px;
}
.container {
  height: 600px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: larger;
}

.input {
  border: none;
  border-bottom: 2px solid blue;
  text-align: center;
  width: 40%;
  margin: 20px;
  font-size: large;
}

.input:focus {
  border-bottom: 2px solid red;
}

.input-form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.text {
  margin-bottom: 100px;
}

.button {
  border-radius: 8px;
  padding: 10px 30px;
  background-color: white;
  color: black;
  border: 2px solid red;
  font-size: larger;
}

.button:hover {
  background-color: red;
  color: white;
  box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
    0 17px 50px 0 rgba(0, 0, 0, 0.19);
}

.please {
  margin-top: 40px;
}

.top-img {
  height: 120px;
  width: 150px;
}

.top-title {
  margin: 0%;
  padding: 8px 0;
  font-size: 16px;
}

a {
  text-decoration: none;
  color: rgb(25, 119, 57);
}

.top-desc {
  font-size: 14px;
  margin-bottom: 0;
  padding-bottom: 0;
  text-align: left;
  margin-left: 1%;
}

.top-info {
  font-size: 12px;
  display: flex;
  justify-content: flex-end;
  padding-right: 20px;
  padding-top: 1%;
  margin-top: 0%;
}

.top-container {
  border: 1px solid black;
  display: grid;
  grid-template-columns: 1fr 6fr;
  padding: 2px;
  margin: 5px;
  height: 120px;
}

.article-body {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
  justify-items: center;
}

.article-text {
  display: grid;
  grid-template-rows: 1fr 10fr;
}

label {
  margin-right: 30px;
}

span {
  font-weight: 600;
}

.image-only {
  margin: 0%;
  padding: 0%;
}

.time-label {
  margin: 0%;
  padding: 0%;
  align-self: top;
  justify-self: top;
  color: black;
}

.corona-div {
  border: 1px solid black;
  flex-wrap: wrap;
  flex-basis: 100%;
  text-align: center;
  border: 1px solid black;
  padding: 2%;
  margin: 1%;
}
.corona-div-wrapper {
  display: flex;
  justify-content: space-around;
  align-content: flex-start;
  flex-wrap: wrap;
}
.covid-control {
  padding-top: 10px;
}

.one-user {
  display: grid;
  grid-template-rows: 1fr 1.2fr;
  border: 1px solid black;
  padding: 0px;
  margin: 0px;
  width: 300px;
  height: 480px;
  margin: 8px;
  border-radius: 10px;
  box-shadow: 1.5px 1.5px 2px;
}

.user-list {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: center;
  background-color: rgb(236, 225, 225);
}

.profile-holder {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  height: 300px;
  width: 300px;
  background-image: url(data:image/png;base64,);
  background-size: cover;
  background-position: center;
  background-color: rgb(184, 103, 103);
}

.profile-pic {
  height: 300px;
  width: 300px;
}

h4 {
  font-weight: 400;
  font-size: large;
}

label {
  display: block;
  padding: 8px 0;
  color: white;
  text-align: center;
}

.user-label {
  background-color: rgb(78, 78, 78);
  display: grid;
  justify-items: center;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.user-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.user-grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}
.user-control {
  padding-top: 10px;
}
.filter-control {
  width: 300px;
  transition: all 1s ease-in-out;
  margin-bottom: 10px;
  border-radius: 6px;
  border: none;
  outline: none;
  background: rgb(64, 109, 168);
  height: 35px;
  color: whitesmoke;
  text-align: center;
  font-size: 18px;
  letter-spacing: 1px;
}

.btn-form {
  background-color: rgb(78, 78, 78);
  margin: 8px;
  color: whitesmoke;
  width: 32px;
  height: 32px;
  font-weight: 700;
  border: none;
  outline: none;
  box-shadow: 1px 1px 1.5px black;
  border-radius: 6px;
}

.btn-form:hover {
  background-color: rgb(37, 37, 37);
}
body {
  font-family: "Roboto", sans-serif;
  width: 1200px;
  text-align: center;
}

html,
body {
  height: 100%;
}

html {
  display: table;
  margin: auto;
}

body {
  display: table-cell;
  vertical-align: middle;
}

.weather-widget {
  transform: translateX(7%);
  text-align: center;
}

@media (max-width: 1000px) {
  html {
    min-height: 600px;
    margin: 0;
  }
  body {
    margin: 0;
    background-color: rgba(255, 0, 0, 0.507);
  }
  .header-container {
    display: flex;
    width: auto;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
    height: 300px;
  }
  .weather-widget {
    display: none;
  }
  .nav-buttons {
    display: flex;
    flex-grow: 1;
  }
  .top-container {
    overflow: hidden;
    background-color: antiquewhite;
  }
  .user-grid {
    display: flex;
    flex-wrap: wrap;
  }
}

@media (max-width: 680px) {
  html {
    min-height: 600px;
    margin: 0;
  }
  body {
    margin: 0;
    background-color: rgba(255, 0, 0, 0.507);
  }
  .header-container {
    display: flex;
    width: auto;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
    height: 270px;
  }
  .weather-widget {
    display: none;
  }
  .header-button {
    margin: 0px;
    padding: 0px;
  }
  .header-button1 {
    padding: 0;
    margin: 0;
    height: 40px;
    width: 100px;
    font-size: smaller;
    text-align: center;
  }
  .logo {
    padding: 0;
    margin: 0;
  }
  .nav-buttons {
    display: flex;
  }
  .top-container {
    overflow: hidden;
    background-color: antiquewhite;
  }
  .user-grid {
    display: flex;
    flex-wrap: wrap;
  }
  .header-input {
    margin: 0;
    padding: 0;
    margin-bottom: 6px;
  }
  .nav-btn {
    margin: 0px;
    padding: 0px;
    height: 40px;
    width: 57px;
    font-size: x-small;
  }
}

